import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)
// 懒加载
const login = () => {

  return Promise.resolve(import('../paga/login'))

}
const index = () => {
  return Promise.resolve(import('../paga/index'))
}
const food = () => Promise.resolve(import('../paga/food'))
const foodDetail = () => import('../paga/foodDetail')
const move = () => import('../paga/move')
const moveDetail = () => import('../paga/moveDetail')
const home = () => import('../components/home')
const mine = () => import('../components/mine')
const order = () => import('../components/order')
// import login from "../paga/login"
// import index from "../paga/index"
// import food from "../paga/food"
// import foodDetail from "../paga/foodDetail"
// import move from "../paga/move"
// import moveDetail from "../paga/moveDetail"
// import home from "../components/home"
// import mine from "../components/mine"
// import order from "../components/order"
const router = new Router({
  mode: "hash",
  routes: [
    {
      path: '/login',
      name: '登录',
      component: login,
    },
    {
      path: '/index',
      component: index,
      children: [
        // alias:别名
        {
          path: "home",
          name: '首页',
          component: home,
          alias: 'h'
        },
        {
          path: "mine",
          name: '我的',
          component: mine
        },
        {
          path: "order",
          name: "订单",
          component: order
        },
        {
          path: '',
          redirect: "home"
        }
      ]
    },
    // 路由独享守卫
    {
      path: '/food',
      component: food,
      beforeEnter(to, from, next) {
        var role = localStorage.getItem('isRole')
        if (role === '1') {
          next()
        } else {
          next('/login')
        }
      }
    },
    {
      path: '/foodDetail',
      component: foodDetail,
    },
    // 路由独享守卫
    {
      path: '/move',
      component: move,
      beforeEnter(to, from, next) {
        let role = localStorage.getItem('isRole')
        if (role === '0') {
          next()
        } else {
          next('/login')
        }
      }
    },
    {
      path: '/moveDetail/:id',
      component: moveDetail,
    },
  ]
})
// 全局守卫
// 进来之前
router.beforeEach((to, from, next) => {
  // 如果是login进来就放行
  if (to.path == '/login') {
    next()
    return
  }
  // 取出登录本地存储的值
  let isLogin = localStorage.getItem('isLogin');
  // 如果本地存储有值得话，就放行
  if (isLogin) {
    next()
    return
  }

  // 否则到login
  next('/login')

})
// 离开之后
router.afterEach(route => {
  console.log(route, "我离开了")

})
export default router


